/* Education CSS */
.MainContainer {
  height: 500px;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding-left: 100px; */
}

.Header {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.Header h1 {
  color: #bababa;
  font-family: "Bebas Neue", sans-serif;
  font-size: 60px;
  padding-right: 20px;
}

.SubContainer {
  display: flex;
  flex-direction: row;
  width: 50%;
}

.NavButtons {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.NavButtons button {
  width: 80%;
  height: 80px;
  color: #bababa;
  padding-left: 3%;
  text-align: left;
  font-family: "Bebas Neue", sans-serif;
  font-size: 20px;
  background-color: Transparent;
  border-left: 5px solid #292929;
  border-bottom: none;
  border-top: none;
  border-right: none;
  transition: background-color 0.25s;
  transition: color 0.25s;
}

.NavButtons button:hover {
  background-color: #292929;
  color: #fdfffc;
  border-left: 5px solid #468189;
}

.NavButtons button:focus {
  background-color: #292929;
  color: #fdfffc;
  border-left: 5px solid #468189;
  outline: 0;
}

.SchoolName {
  width: 60%;
}

.SchoolName h1 {
  background: -webkit-linear-gradient(30deg, #0076a5, #ab0c2f);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Bebas Neue", sans-serif;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 20px;
}

.SchoolName p {
  margin-top: -25px;
  color: #bababa;
  font-family: "Bebas Neue", sans-serif;
  font-size: 30px;
}

.CourseworkHeader h1 {
  color: #468189;
  font-family: "Bebas Neue", sans-serif;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: -5px;
}

.ProjectsHeader h1 {
  color: #468189;
  font-family: "Bebas Neue", sans-serif;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: -5px;
}

.Classes li {
  color: #bababa;
  font-family: "Bebas Neue", sans-serif;
  font-size: 30px;
}

/* Experience CSS */
.Job {
  display: flex;
  flex-direction: column;
  width: 70%;
}

.JobInfo h1 {
  background: -webkit-linear-gradient(30deg, #0076a5, #ab0c2f);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Bebas Neue", sans-serif;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 20px;
}

.JobInfo p {
  margin-top: -25px;
  color: #bababa;
  font-family: "Bebas Neue", sans-serif;
  font-size: 30px;
}

.Playroll h1 {
  color: #468189;
  font-family: "Bebas Neue", sans-serif;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 20px;
}

.Playroll p {
  margin-top: -25px;
  color: #bababa;
  font-family: "Bebas Neue", sans-serif;
